.barra {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 3rem;
  @include tablet {
    flex-direction: row;
    justify-content: space-between;
  }
}

.navegacion {
  display: none;

  @include tablet {
    display: flex;
    align-items: center;
  }

  a {
    color: $blanco;
    text-decoration: none;
    font-size: 2.2rem;
    display: block;
    text-align: center;

    &:hover {
      color: $verde;
    }

    @include tablet {
      margin-right: 2rem;
      font-size: 1.8rem;

      &:last-of-type {
        margin-right: 0;
      }
    }
  }
}

/**Menú Hamburguesa**/

.mobile-menu {
  @include tablet {
    display: none;
  }

  img {
    width: 5rem;
  }
}

.mostrar {
  display: block;
}
